<template>
     <div class="col-md-6 col-md-offset-3 ">
         <div class="row">

         <div class="col-md-3 text-center col-md-offset-5">
             <form >
                   <!-- <form> -->
    <!-- <label class="radio-inline">
      <input type="radio" name="optradio">Option 1
    </label>
    <label class="radio-inline">
      <input type="radio" name="optradio">Option 2
    </label>
    <label class="radio-inline">
      <input type="radio" name="optradio">Option 3
    </label> -->
  <!-- </form> -->
                 <label class="radio-inline" v-for="paymentSystem in paymentSystems">
    <input type="radio" :value="paymentSystem.id" name="1234" v-model="pickedSystem">
    {{paymentSystem.name}}</label>
                <!-- <checkout-component 
                v-for="paymentSystem in paymentSystems" :paymentSystem="paymentSystem"></checkout-component> -->
            </form>
         </div>
         </div>
         <div class="text-center">
                
            <button class="btn btn-primary" type="submit"
            v-on:click="back">Back</button>
            <button class="btn btn-primary" type="submit"
            v-on:click="chekcout">Checkout</button>
         </div>
      </div>
</template>

<script>
// import CheckoutComponent from '@/components/checkoutComponent'
export default {
    data() {
        return {
            paymentSystems: '',
            pickedSystem: ''
        }
    },
    methods: {
        chekcout: function () {
            var self = this;
            var userId = this.$parent.getCookie('id');
            // console.log(userId);
            // console.log(this.pickedSystem);
            // console.log(this.$route.params.price);

            fetch(this.$parent.orderUrl, {
                method: 'post',
                headers: {  
                    "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
                },  
                body: 'userId='+JSON.stringify(userId)+'&paymentId='+JSON.stringify(this.pickedSystem)
                +'&totalPrice='+JSON.stringify(this.$route.params.price)
            })
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function (data) {
                console.log(data);
                if(data != false){
                    alert('Thank you for your purchase');
                    self.$router.push({ path: '/profile' });
                }
            });

        },
        back: function () {

        }
    },
    components:{
        // CheckoutComponent
    },
    created () {
        var self = this;
        fetch(this.$parent.paymentUrl,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
            console.log(data)
            if(data != false)
            {
                self.paymentSystems = data;       
            }
      });
    }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* button{
    display: block;
} */
</style>



