<template>
    <div>
        <div class="col-md-2 menu">       
            <div >
                <div>
                    <div class="authors" v-on:click="showUnshowAuthors">
                        Authors
                    </div>
                    <div v-for="authorInfo in options.authors" v-bind:class="activeNoActiveAuthors" >
                        <label class="radio-inline link-text">
                            <input  type="radio" :value="authorInfo.id" name="1234" v-model="author">
                        {{authorInfo.name}} {{authorInfo.surname}}</label>
                    </div>
                </div>
            </div>

                <div>
                    <div class="genres" v-on:click="showUnshowGenres">
                        Genres
                    </div>
                        <div v-for="genreInfo in options.genres" v-bind:class="activeNoActiveGenres" >
                            <label class="radio-inline link-text">
                                <input  type="radio" :value="genreInfo.id" name="145234" v-model="genre">
                                {{genreInfo.name}}</label>
                        </div>

                </div>

                <div>
                    <div class="books" v-on:click="showUnshowBooks">
                        Books
                    </div>
                        <ul>
                            <li class="link-text" v-bind:class="activeNoActiveBooks"
                            v-for="book in options.books"
                            v-on:click="showBook(book.id)">{{book.name}}</li>
                        </ul>
                </div>
            </div>

            <div class="col-md-7 content">
                <div class="book"v-for="a in asd">
                    <div>
                        <img src="" class="bookImg"/>
                        <div class="bookInfo">
                            <label>Book naame</label>
                            <p>{{a.name}}</p>
                            <label>Book description</label>
                            <p>{{a.description}}</p>
                            <button class="btn btn-info"
                            v-on:click="showBook(a.id)">More info</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

    </div>
</template>

<script>

export default {
    name: 'HelloWorld',
    data() {
        return {
            ad: {1:'a',2:'s'},
            author: '',
            genre: '',
            loginStats: false,
            role: '',
            activeNoActiveAuthors: {
                active: false,
                noActive: true
                },
            activeNoActiveGenres: {
                active: false,
                noActive: true
            },
             activeNoActiveBooks: {
                active: false,
                noActive: true
            },
            options: {
                authors: '',
                genres: '',
                books: ''
            }
        }
    },
    methods: {
        showUnshowAuthors: function () {
            this.showUnshow(this.activeNoActiveAuthors);
        },
        showUnshowGenres: function () {
            this.showUnshow(this.activeNoActiveGenres);
        },
        showUnshowBooks: function () {
            this.showUnshow(this.activeNoActiveBooks);
        },
        showUnshow: function (object) {
            if(object.active == true){
                object.active = false;
            }else{
                object.active = true;
            }

            if(object.noActive == true){
                object.noActive = false;
            }else{
                object.noActive = true
            }
        },
        showBooksByAuthor: function (id) {
            this.author = id;
        },
        showBooksByGenre: function (id) {
            console.log(id)
            this.genre = id;
            console.log(id)
        },
        // login: function () {
        //     this.$router.push({ path: 'login' });
        // },
        // register: function () {
        //     this.$router.push({ path: 'register' });
        // },
        // logout: function () {
        //     document.cookie ='id =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        //     document.cookie ='hash =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        //     document.cookie ='role =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        //     this.loginStats = false;
        //     this.$router.push({ path: '/' });
        // },
        // adminPanel: function () {
        //     this.$router.push({ path: '/adminPanel' });
        // },
        showBook: function (id){
            // console.log(id)
            this.$router.push({ path: '/BookInfo/'+id });
            
        }
    },
    computed: {
        asd (){

            var filterResult = {};
            // console.log(321312)
            // function filterBy(field,self)
            // {
            //     console.log(field);
            //     var i =0;
                //     for(var book in self.options.books)
                //     {
                // // console.log(self.options.books[book][field+'s'][self[field]] != undefined);
                //         // console.log(self.options.books[book][field+'s'][self[field]])
                //         if(self.options.books[book][field+'s'][self[field]]){
                //             filterResult[book] = self.options.books[book];
                //             i++;
                //         }
                //     }
                    // if(i ==0){
                    //     filterResult = {};
                    // }
                    // console.log(self[field]);
                    // self[field] = '';
                    // console.log(filterResult)
            //     return filterResult;
            // }
            // console.log('author' +this.author);
            // console.log('genre' +this.genre);
            // console.log('author'+ this.author);

            console.log(this.author);
            console.log(this.genre);
            if(this.author !='')
            {
                                    for(var book in this.options.books)
                    {
                // console.log(self.options.books[book][field+'s'][self[field]] != undefined);
                        // console.log(this.options.books[book].authors[this.author])
                        if(this.options.books[book].authors[this.author]){
                            filterResult[book] = this.options.books[book];
                            // i++;
                        }
                    }
                    this.author = '';

                    // console.log(filterResult)
                // var filterResult = filterBy('author',this);
                // console.log('author');
                // console.log(this.author);
            } else if (this.genre !='') {
                                     for(var book in this.options.books)
                    {
                // console.log(self.options.books[book][field+'s'][self[field]] != undefined);
                        // console.log(this.options.books[book].genres[this.genre])
                        if(this.options.books[book].genres[this.genre]){
                            filterResult[book] = this.options.books[book];
                            // i++;
                        }
                    }
                    this.genre = '';
            }else{
                var filterResult = this.options.books;
            }

            return filterResult;

        }
    },
    created() {
        var self = this;

        // console.log(authorUrl);
        // var id = this.$parent.getCookie('id');
        // var hash = this.$parent.getCookie('hash');
        // var role = this.$parent.getCookie('role');
        // // console.log(id)
        // // console.log(hash)
        // // console.log(role)
        // if(id == undefined && hash == undefined&& role == undefined)
        // {
        //     this.loginStats = false;
        // } else{
        //     this.loginStats = true;
        //     this.role = role;
        // }
// console.log(this.loginStats);
        fetch(authorUrl,{  
                method: 'get'})
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function(data){
            self.options.authors = data;       
        });

        fetch(genreUrl,{  
                method: 'get'})
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function(data){
            self.options.genres = data;        
        });

        fetch(bookUrl,{  
                method: 'get'})
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function(data){
            self.options.books = data;     
        });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* .login,
.register,
.profile,
.logout {
    float: right;
    margin-right: 50px;
    margin-top: 20px;
} */

.menu {
    padding-left: 40px;
}

.menu input{
    visibility: hidden;
}

.menu label:hover{
    background-color: #F0F8FF;
}

.active{
    display: block;
    /* padding-left: 20px; */
}
.noActive{
    display: none;
}

.link-text{
    color:#0000FF;
    text-decoration: underline;
    cursor: pointer;
}
.authors,.genres,.books {
    /* color:darkturquoise; */
    color: 	#6495ED;
    font-size: 20px;
    cursor: pointer;
}

.bookImg{
    /* float: left; */
    width: 190px;
    height: 280px;
}
.bookInfo{
    /* float: left; */
    margin-left: 25px;
    display: inline-block;
}
.book{
    border: 1px solid grey;
    margin-bottom: 10px;
    /* display: block; */
}
.book > div{
    margin: 5px 5px;
}
/* h1, h2 {
  font-weight: normal;
}

