<template>
    <div>
        <form @submit.prevent="validateBeforeSubmit" >
            <div class="form-group" :class="{'has-error': errors.has('name') }" >
                <label class="control-label" for="name">Book description</label>
                <input v-model="name"
                v-validate.initial="name" 
                data-vv-rules="required" 
                class="form-control" type="text" placeholder="name">
                <p class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</p>
            </div>

            <div class="form-group" :class="{'has-error': errors.has('description') }">
                <label class="control-label" for="description">Book Description</label>
                <textarea v-model="description"
                v-validate.initial="description"
                data-vv-rules="required"
                class="form-control" rows="3" placeholder="Description"></textarea>
                <p class="text-danger" v-if="errors.has('description')">{{ errors.first('description') }}</p>
            </div>

            <div class="form-group" :class="{'has-error': errors.has('price') }" >
                <label class="control-label" for="price">Book Price</label>
                <input v-model="price"
                v-validate.initial="price" 
                data-vv-rules="required|numeric" 
                class="form-control" type="text" placeholder="Price">
                <p class="text-danger" v-if="errors.has('price')">{{ errors.first('price') }}</p>
            </div>
            
            <div class="form-group" :class="{'has-error': errors.has('discount') }" >
                <label class="control-label" for="discount">Book Discount</label>
                <input v-model="discount"
                v-validate.initial="discount" 
                data-vv-rules="required|numeric" 
                class="form-control" type="text" placeholder="Discount">
                <p class="text-danger" v-if="errors.has('discount')">{{ errors.first('discount') }}</p>
            </div>
            

            <div class="row">
                <div class="col-md-6 form-group":class="{'has-error': errors.has('bookGenres') }" >
                    <label class="control-label" for="bookGenres">Select Genre(s)</label>
                    <select v-validate.initial="bookGenres"
                    data-vv-rules="required" 
                    class="form-control select" multiple>
                        <option v-if="genre.id !=null"
                        v-for="genre in bookGenres" 
                        :selected="genres[genre.id]" 
                        v-on:click="addRemoveGenre($event)"
                        :value="genre.id">{{genre.name}}</option>
                        <option v-for="genre in genres" v-if="!bookGenres[genre.id]" v-on:click="addRemoveGenre($event)"
                        :value="genre.id">{{genre.name}}</option>
                    </select>
                    <p class="text-danger" v-if="errors.has('bookGenres')">{{ errors.first('bookGenres') }}</p>
                </div>

                <div class="col-md-6 form-group":class="{'has-error': errors.has('bookAuthors') }" >
                    <label class="control-label" for="bookAuthors">Select Author(s)</label>
                    <select v-validate.initial="bookAuthors"
                    data-vv-rules="required" 
                    class="form-control select" multiple >
                        <option v-if="author.id !=null"
                        v-for="author in bookAuthors" 
                        :selected="authors[author.id]" 
                        v-on:click="addRemoveAuthor($event)"
                        :value="author.id">{{author.name}} {{author.surname}}</option>
                        <option v-for="author in authors" v-if="!bookAuthors[author.id]" v-on:click="addRemoveAuthor($event)"
                        :value="author.id">{{author.name}} {{author.surname}}</option>
                    </select>
                    <p class="text-danger" v-if="errors.has('bookAuthors')">{{ errors.first('bookAuthors') }}</p>
                </div>
            </div>

            <button class="btn" type="submit">Change Book</button>

        </form>
  </div> 
</template>

<script>
export default {
  data () {
    return {
        id: '',
      name: '',
      description: '',
      price: '',
      discount: '',
      bookGenres: {},
      bookAuthors: {},
      oldAuthors: '',
      oldGenres: ''
    }
},
methods: {
        validateBeforeSubmit(e) {
        var self = this;
        this.$validator.validateAll();

        if (!this.errors.any()) {
            this.$emit('changeBook',this.id,this.name,this.description,this.price,this.discount,
                    this.bookGenres,this.bookAuthors,this.oldGenres,this.oldAuthors);
        }
    },
    addRemoveAuthor: function (event) {
        if(event.ctrlKey === false){
            this.bookAuthors = {}
            this.bookAuthors[event.target.value] = this.authors[event.target.value];
        } else{
            if(this.bookAuthors[event.target.value]){
                delete this.bookAuthors[event.target.value];
            }else{
                this.bookAuthors[event.target.value] = this.authors[event.target.value];
            }
        }
    },
    addRemoveGenre: function (event) {
        if(event.ctrlKey === false){
            this.bookGenres = {}
            this.bookGenres[event.target.value] = this.genres[event.target.value];
        } else{

            if(this.bookGenres[event.target.value]){
                delete this.bookGenres[event.target.value];
            }else{
                this.bookGenres[event.target.value] = this.genres[event.target.value];
            }
        }
    }
},
props: ['genres',
        'authors',
        'book'],
created () {
    this.id = this.book.id;
    this.name  = this.book.name;
    this.description  = this.book.description;
    this.price  = this.book.price;
    this.discount  = this.book.discount;
    this.bookGenres = this.book.genres;
    this.bookAuthors = this.book.authors;
console.log(this.book.genres);
    this.oldGenres = JSON.stringify(this.book.genres);
    this.oldAuthors= JSON.stringify(this.book.authors);

}
}
    //   <!-- <show-add-book v-if="showAddBook== true" @addBook="addBook" :genres="options.genres"
    //   :authors="options.authors"></show-add-book> -->
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



