<template>
<div>
                <div class="tableRow"
                v-on:click="showAdditionalInfo(orderComponents.id)">
                    <div class="cell nuumber">
                        {{orderComponents.id}}
                    </div>
                    <div class="cell date">
                        {{orderComponents.createDate}}
                    </div>
                    <div class="cell sum">
                        {{orderComponents.totalPrice}}
                    </div>
                    <div class="cell status">
                        {{orderComponents.name}}
                    </div>
                    </div>
                <div class="orderInfo col-md-offset-2"
                 v-bind:style="{ display: display}"
                 v-for="aditionalPart in aditionalInfo">
                    <div >
                        <label >Name</label>
                        {{aditionalPart.name}}
                    </div>
                    <div >
                        <label >Price:</label>
                        {{aditionalPart.bookPrice}}$
                    </div>
                    <div >
                        <label >Discount:</label>
                        {{aditionalPart.bookDiscount}}%
                    </div>
                    <div >
                        <label >Count:</label>
                        {{aditionalPart.count}}
                    </div>
                    
                </div>
                </div>
                <!-- </div> -->
            <!-- </table> -->

<!-- </div> -->
</template>

<script>
export default {
  data () {
    return {
        display: 'none',
        aditionalInfo: ''
    }
},
methods: {
    showAdditionalInfo: function (orderId) {
        var id = this.$parent.$parent.$parent.getCookie('id');
        var self = this;
        fetch(this.$parent.$parent.$parent.orderUrl+id+'/'+orderId,{  
            method: 'get'})
        .then(this.$parent.$parent.$parent.status)
        .then(this.$parent.$parent.$parent.json)
        .then(function(data){
            // console.log(data);
            if(data != false && data != null)
            {
                self.aditionalInfo = data;
                // self.clientDiscount = data[0]['clientDiscount'];
            }      
        });
        // console.log('dsa')

        if(this.display == 'none'){
            this.display = 'block';
        }else{
            this.display = 'none';
        }
    }
},
props: ['orderComponents'],
created () {
    // this.display = 'table-row';
    console.log(this.orderComponents)
},
}
    //   <!-- <show-add-book v-if="showAddBook== true" @addBook="addBook" :genres="options.genres"
    //   :authors="options.authors"></show-add-book> -->
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* td{
    text-align: center;
} */

.tableRow{
    cursor: pointer;
}
.tableRow:hover{
    background-color: #FFE4C4;
}
.orderInfo:hover{
    background-color: #DEB887;
}
.orderInfo{
    text-align: left;
}
/* .cell{
    display: table-cell; */
    /* border-left: 1px solid */
    /* font-weight: bold; */
/* } */
.cell{
    display: table-cell;
    /* border-left: 1px solid */
    /* font-weight: bold; */
}
.additionalInfo{
    display:none;
}

.nuumber{
    width: 33px;
}

.date{
    width: 361px;
}

.sum {
    width: 86px;
}

.status{
    width: 187px;
}
</style>



