<template>
<div>
    <!-- <div class="row">
        <div v-if="loginStats == false" class="row">
            <button type="button" class="btn btn-success register"
            v-on:click="register" >Register</button>
            <button type="button" class="btn btn-success login"
            v-on:click="login">Login</button>
        </div>
        <div v-else class="row">
            <button type="button" class="btn btn-success profile"
            v-if="role == 'admin'"
            v-on:click="adminPanel" >Admin Panel</button>
            <button v-if="role == 'user'"type="button" class="btn btn-success profile" >Profile</button>
            <button type="button" class="btn btn-success logout"
            v-on:click="logout" >Logout</button>
        </div>
    </div> -->

    <div>
        <div class="col-md-8 content">
            <div class="book">
                <div>
                    <img src="" class="bookImg"/>
                    <div class="bookInfo">
                        <h3>{{name}}</h3>
                        <div class="">
                            <label>Description</label>
                            <p>{{description}}</p>
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                                <label>Book authors</label>
                                <ul>
                                    <li v-for="author in authors">
                                        {{author}}
                                    </li>
                                </ul>
                            </div>
                            <div class="col-md-6 title">
                                <label>Book genres</label>
                                <ul>
                                    <li v-for="genre in genres">
                                        {{genre}}
                                    </li>
                                </ul>
                            </div>    
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                                <label>Book price</label>
                                <p>{{price}}</p>
                            </div>
                            <div class="col-md-6">

                                <label>Book discount</label>
                                <p>{{discount}}%</p>
                            </div>
                        </div>

                        <button class="btn btn-info"
                        v-if="loginStats" v-on:click="addToBag">Add to Bag</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>    
</template>

<script>

export default {
    name: 'HelloWorld',
    data() {
        return {
            bookId: '',
            userId: '',
            name: '',
            price: '',
            description: '',
            discount: '',
            authors: '',
            genres: '',
            loginStats: false,
            role: '',
            activeNoActiveAuthors: {
                active: false,
                noActive: true
                },
            activeNoActiveGenres: {
                active: false,
                noActive: true
            },
             activeNoActiveBooks: {
                active: false,
                noActive: true
            },
            options: {
                authors: '',
                genres: '',
                books: ''
            }
        }
    },
    methods: {
        // login: function () {
        //     this.$router.push({ path: '/login' });
        // },
        // register: function () {
