        //     document.cookie ='hash =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        //     document.cookie ='role =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        //     this.loginStats = false;
        //     this.$router.push({ path: '/' });
        // },
        // adminPanel: function () {
        //     this.$router.push({ path: '/adminPanel' });
        // },
        addToBag: function () {
            //zapros na dobavlenie v korzinu
            // console.log(this.bookId)
            var userId = this.$parent.getCookie('id');
            // console.log(id)
                  var self = this;
            fetch(this.$parent.bagUrl, {
                method: 'post',
                headers: {  
                    "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
                },  
                body: 'bookId='+JSON.stringify(this.bookId)+'&clientId='+JSON.stringify(userId)
                +'&count='+JSON.stringify(1)
            })
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function (data) {
                console.log(data)
                if(data != false){
                    // fetch(self.$parent.genreUrl,{  
                    //     method: 'get'})
                    // .then(self.$parent.status)
                    // .then(self.$parent.json)
                    // .then(function(data){
                    // self.options.genres = data;       
                    // });
                    alert('Book successful added to bag');
                    // self.unshowWorkFiel();
                }
            });
        }
    },
    created() {
        var self = this;
        // console.log(this.$route.params.id);
        // var self = this;
        // var id = this.$parent.getCookie('id');
        // var hash = this.$parent.getCookie('hash');
        // var role = this.$parent.getCookie('role');

        // this.userId = id;
        // // console.log(id)
        // // console.log(hash)
        // // console.log(role)
        // if(id == undefined && hash == undefined&& role == undefined)
        // {
            // } else{
                //     this.loginStats = true;
        //     this.role = role;
        // }
            this.loginStats = this.$parent.loginStatus;
// console.log(this.loginStats);

        fetch(this.$parent.bookUrl+this.$route.params.id,{  
                method: 'get'})
            .then(this.$parent.status)
            .then(this.$parent.json)
            .then(function(data){
                console.log(data)
                if(data != false)
                {
                    // console.log(data.id);
                    self.bookId = data.id;
                    self.name = data.name;
                    self.price = data.price;
                    self.description = data.description;
                    self.discount = data.discount;
                    self.authors = data.authors.split(',');
                    self.genres = data.genres.split(',');
                }
            // self.options.authors = data;       
        });
                // console.log(this.id);

        // fetch(this.$parent.genreUrl,{  
        //         method: 'get'})
        //     .then(this.$parent.status)
        //     .then(this.$parent.json)
        //     .then(function(data){
        //     self.options.genres = data;        
        // });

        // fetch(this.$parent.bookUrl,{  
        //         method: 'get'})
        //     .then(this.$parent.status)
        //     .then(this.$parent.json)
        //     .then(function(data){
        //     self.options.books = data;     
        // });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /* .login,
    .register,
    .profile,
    .logout {
        float: right;
        margin-right: 50px;
        margin-top: 20px;
    } */
    .bookImg{
    float: left;
    width: 250px;
    height: 380px;
}
.bookInfo{
    /* float: left; */
    margin-left: 25px;
    display: inline-block;
}

.bookInfo li{
   width:200px;
}
</style>



