<template>
     <div
      v-if="loginStatus"
      class="col-md-6 col-md-offset-3">
        <table class="table table-bordered">
               <tr>
                    <th></th>
                    <th>N</th>
                    <th>Name</th>
                    <th>Count</th>
                    <th>Price</th>
                    <th>discount</th>
                    <th>Sum with discount</th>
                </tr>
                <bag-component 
                v-if="bag"v-for="component in bag" 
                :bagComponents="component"
                @changeCount="changeCount"
                @deleteBagComponents="formateDeleteArr"
                @noDelete="removeFromDelete"></bag-component>
        </table>
        <!-- <div class="row"> -->
            <button class="btn"
            v-on:click="deleteCompoents">Delete Selected from Bag</button>
        <!-- </div> -->
                <div class="col-md-offset-8">
                    <div class="row">
                        <div class="leftCol">
                            <p>client discont</p>
                        </div>
                        <div class="rightCol">
                            {{clientDiscount}}%
                        </div>
                    </div>

                    <div class="row">
                        <div class="leftCol">
                            <p>Total book discount</p>
                        </div>
                        <div class="rightCol">
                            {{totalBookDiscount}}$
                        </div>
                    </div>

                    <div class="row">
                        <div class="leftCol">
                            <p>Total user discount</p>
                        </div>
                        <div class="rightCol">
                            {{totalUserDiscount}}$
                        </div>
                    </div>

                    <div class="row">
                        <div class="leftCol">
                            <p>Total Sum</p>
                        </div>
                        <div class="rightCol">
                            {{sumWithAllDisc}}$
                        </div>
                    </div>
                </div>

                <button class="btn checkout"
                v-on:click="toCheckout">To checkout</button>
    </div>
  </div>
    
</template>

<script>

import BagComponent from '@/components/bagComponents/bagComponent'

export default {
    name: 'HelloWorld',
    data() {
        return {
            bag: '',
            userId: '',
            clientDiscount: 0,
            totalSum: '',
            sumWithDisc: '',
            userDiscount: '',
            booksDiscount: '',
            deleteArr: [],
            loginStatus: false
        }
    },
    methods: {
        changeCount: function (count,id){
            var self = this;
            fetch(this.$parent.$parent.bagUrl, {
                method: 'put',
                headers: {  
                    "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
                },  
                body: 'userId='+JSON.stringify(this.userId)+'&bookId='+JSON.stringify(id)
                +'&count='+JSON.stringify(count)
            })
            .then(this.$parent.$parent.status)
            .then(this.$parent.$parent.json)
            .then(function (data) {
                console.log(data);
                if(data == false){
                    alert('Some error');
                }
            });
            for(var key in this.bag)
            {
                if(this.bag[key]['id'] == id){
                    this.bag[key]['count'] = count;
                }

            }
        },
        formateDeleteArr: function (id) {
            this.deleteArr.push(id);
        },
        removeFromDelete: function (id) {
            this.deleteArr.splice(this.deleteArr.indexOf(id),1);
        },
        deleteCompoents: function () {
            var self = this;
            if(this.deleteArr[0] != undefined){
                fetch(this.$parent.$parent.bagUrl, {
                    method: 'delete',
                    headers: {  
                        "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
                    },  
                    body: 'userId='+JSON.stringify(this.userId)+'&deleteArr='+JSON.stringify(this.deleteArr)
                })
                .then(this.$parent.$parent.status)
                .then(this.$parent.$parent.json)
                .then(function (data) {

                    if(data == true){
                        for(var key in self.deleteArr){
                            self.$delete(self.bag, key);
                        }
                        alert('Book successful deleted from Bag');
                    }
                });
            }
        },
        toCheckout: function () {
            this.$router.push({ path: '/checkout/'+this.sumWithDisc });
        }
        // login: function () {
        //     this.$router.push({ path: '/login' });
        // },
        // register: function () {
        //     this.$router.push({ path: 'register' });
        // },
        // logout: function () {
        //     document.cookie ='id =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        //     document.cookie ='hash =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        //     document.cookie ='role =; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        //     this.loginStats = false;
        //     this.$router.push({ path: '/' });
        // },
        // adminPanel: function () {
        //     this.$router.push({ path: '/adminPanel' });
        // },
        // addToBag: function () {
        //     //zapros na dobavlenie v korzinu
        // }
    },
    created() {
        var self = this;
        // // console.log(this.$route.params.id);
        // var self = this;
        var id = this.$parent.$parent.getCookie('id');
        this.userId = id;
        this.loginStatus = this.$parent.$parent.loginStatus;
        // var hash = this.$parent.$parent.getCookie('hash');
        // var role = this.$parent.$parent.getCookie('role');
        // console.log(id)
        // console.log(hash)
        // console.log(role)
        // if(id == undefined && hash == undefined&& role == undefined)
        // {
        //     this.loginStats = false;
        // } else{
        //     this.loginStats = true;
        //     this.role = role;
        // }
// console.log(this.loginStats);

        fetch(this.$parent.$parent.bagUrl+id,{  
                method: 'get'})
            .then(this.$parent.$parent.status)
            .then(this.$parent.$parent.json)
            .then(function(data){
                if(data != false && data != null)
                {
                    self.bag = data;
                    self.clientDiscount = data[0]['clientDiscount'];
                }      
        });
    },
    computed: {
        totalBookDiscount () {
            var totalBookDisc = 0;
            var totalSum = 0;

            for(var key in this.bag){
                var sum = this.bag[key]['price'] * this.bag[key]['count'];
                totalSum += sum;
                totalBookDisc += sum*(this.bag[key]['bookDiscount']/100);
            }

            this.totalSum = totalSum;
            this.booksDiscount = totalBookDisc;

            return totalBookDisc;
        },
        totalUserDiscount () {
            var userDisc = 0;

            if(this.clientDiscount != 0){
                userDisc = (this.totalSum-this.booksDiscount)*(this.clientDiscount/100);
            }
            this.userDiscount = userDisc;

            return userDisc;
        },
        sumWithAllDisc () {
            var sum = this.totalSum - this.booksDiscount - this.userDiscount;
            this.sumWithDisc = sum;
            return sum;
        }
    },
    components: {
        BagComponent
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .login,
    .register,
    .profile,
    .logout {
        float: right;
        margin-right: 50px;
        margin-top: 20px;
    }
    .bookImg{
    float: left;
    width: 250px;
    height: 380px;
}
.bookInfo{
    /* float: left; */
    margin-left: 25px;
    display: inline-block;
}

.leftCol{
    float: left;
    
}

.rightCol{
    float: right;
    margin-right: 15px;
}
.checkout{
    float: right;
}


</style>



