<template>
     <div class="col-md-6 col-md-offset-3">
        <!-- <table class="table table-bordered">
               <tr>
                    <th>N</th>
                    <th>Date of create</th>
                    <th>Sum</th>
                    <th>Status</th>
                </tr>
        </table> -->

                   <div class="table">
                        <div>
                            <div class="cell nuumber">N</div>
                            <div class="cell date">Date of create</div>
                            <div class="cell sum">Sum</div>
                            <div class="cell status">Status</div>
                        </div>
                <order-component 
                v-for="component in orders" 
                :orderComponents="component"></order-component>
                   </div>
  </div>
    
</template>

<script>

import OrderComponent from '@/components/orderComponent'

export default {
    name: 'HelloWorld',
    data() {
        return {
            orders: ''
        }
    },
    methods: {
    
    },
    created() {
        // console.log('sada');;
        var self = this;

        var id = this.$parent.$parent.getCookie('id');
        // this.userId = id;
        // this.loginStatus = this.$parent.$parent.loginStatus;


        fetch(this.$parent.$parent.orderUrl+id,{  
                method: 'get'})
            .then(this.$parent.$parent.status)
            .then(this.$parent.$parent.json)
            .then(function(data){
                console.log(data);
                if(data != false && data != null)
                {
                    self.orders = data;
                    // self.clientDiscount = data[0]['clientDiscount'];
                }      
        });
    },
    computed: {
        
    },
    components: {
        OrderComponent
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

th{
    text-align: center;
}


.table{
    /* display: table; */
    /* border: 1px solid; */
    text-align: center;
}

/* .tableRow{
    display: table-row;

} */

.cell{
    display: table-cell;
    /* border-left: 1px solid */
    font-weight: bold;
}
	
/* .left{
	border-left: 0;
} */

.nuumber{
    width: 33px;
}

.date{
    width: 361px;
}

.sum {
    width: 86px;
}

.status{
    width: 187px;
}
/* div{
    text-align: center;
} */
</style>



