<template>
<!-- <div> -->
            <!-- <table class="table table-bordered">
               <tr>
                    <th>N</th>
                    <th>Name</th>
                    <th>Count</th>
                    <th>Price</th>
                    <th>discount</th>
                    <th>Sum</th>
                </tr> -->
                <tr >
                    <td>
                        <input type="checkbox" :value="bagComponents.id"
                        v-on:click="deleteBagComponents">
                        <!-- {{bagComponents.id}} -->
                    </td>
                    <td>
                        {{bagComponents.posNumber}}
                    </td>
                    <td>
                        {{bagComponents.name}}
                    </td>
                    <td>
                        <button v-on:click="plus(bagComponents)">
                            +
                        </button>
                        {{count}}
                        <button v-on:click="minus(bagComponents)">
                            -
                        </button>
                    </td>
                    <td>
                        {{bagComponents.price}}
                    </td>
                    <td>
                        {{bagComponents.bookDiscount}}%
                    </td>
                    <td>
                        {{sumCalculate}}
                    </td>
                </tr>
            <!-- </table> -->

<!-- </div> -->
</template>

<script>
export default {
  data () {
    return {
        count: '',
        price: '',
        discount: '',
        sum: '',
        id: ''
    }
},
methods: {
    deleteBagComponents: function (event) {
        // console.log(event)
        if(event.srcElement.checked == true)
        {
            this.$emit('deleteBagComponents', event.srcElement.value);
        }else{
            this.$emit('noDelete', event.srcElement.value);
        }
        // console.log(event.srcElement.value);
    },
    // sumCalculate: function(count,price,discount){
    //     var sum = count * price;
    //     var discount = sum*(discount/100);
    //     sum = sum - discount;
    //     return sum;
    // },
    posNumber: function() {
        var newPos = this.startPosNumber+1;
        // // console.log(this.startPosNumber+1)
        // this.startPosNumber = newPos;
        return newPos;
    },
    plus: function () {
        this.count++;
        this.$emit('changeCount', this.count,this.id);
    },
    minus: function () {
        if(this.count == 1){
            return;
        }
        this.count--;
        this.$emit('changeCount', this.count,this.id);
    }
},
props: ['bagComponents'],
created () {
    this.count = this.bagComponents.count
    this.price = this.bagComponents.price
    this.discount = this.bagComponents.bookDiscount
    this.id = this.bagComponents.id
    console.log(this.bagComponents)
    
//     this.a = this.bagComponents
// //     this.id = this.book.id;
// //     this.name  = this.book.name;
// //     this.description  = this.book.description;
// //     this.price  = this.book.price;
// //     this.discount  = this.book.discount;
// //     this.bookGenres = this.book.genres;
// //     this.bookAuthors = this.book.authors;
// // console.log(this.book.genres);
// //     this.oldGenres = JSON.stringify(this.book.genres);
// //     this.oldAuthors= JSON.stringify(this.book.authors);

},
computed: {
    sumCalculate () {
        var sum = this.count * this.price;
        var discount = sum*(this.discount/100);
        sum = sum - discount;
        this.sum = sum;
        // this.$emit('changeCount', this.sum);
        return sum;
    },
}
}
    //   <!-- <show-add-book v-if="showAddBook== true" @addBook="addBook" :genres="options.genres"
    //   :authors="options.authors"></show-add-book> -->
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



