<template>
  <div class="hello">

    <div class="col-md-3 line">
      <div class="adminMenu">
        <div class="authorSection">
          <button class="btn" v-on:click="addAuthorField">Add author</button>
          <label>Edit or Delete Author</label>
          <select class="form-control select"  v-model="author" v-on:change="changeAuthorField" 
            v-on:mouseover='prihodit' >
            <!-- <option value="">Authors</option> -->
            <option v-for="author in options.authors" 
            :value="author.id" >{{author.name}} {{author.surname}}</option>
          </select>
        </div>

        <div class="genreSection">
          <button class="btn" v-on:click="addGenreField">Add genre</button>
          <label>Edit or Delete Genre</label>
          <select class="form-control select" v-model="genre" v-on:change="changeGenreField" 
          v-on:mouseover='prihodit'>
            <!-- <option value="">Genres</option> -->
            <option v-for="genre in options.genres" 
            :value="genre.id">{{genre.name}}</option>
          </select>
        </div>

        <div class="bookSection">
          <button class="btn" v-on:click="addBookField">Add book</button>
          <label>Edit or Delete book</label>
          <select class="form-control select"  v-model="book" v-on:change="changeBookField"
          v-on:mouseover='prihodit'>
            <option v-for="book in options.books" 
            :value="book.id">{{book.name}}</option>
          </select>
        </div>

        <div class="userSection">
          <button class="btn" v-on:click="addUserField">Add user</button>
          <label>Edit or Delete user</label>
          <select class="form-control select"  v-model="user" v-on:change="changeUserField"
          v-on:mouseover='prihodit'>
            <option v-for="user in options.users" 
            :value="user.id">{{user.name}} {{user.surname}}</option>
          </select>
        </div>
      </div>  
    </div>
    <div class="col-md-9 workField">
      <router-view name="addNewAuthor"
      @addAuthor="addAuthor"></router-view>

      <router-view name="addNewGenre"></router-view>
      <router-view name="addNewBook"
       :genres="options.genres"
       :authors="options.authors"></router-view>
      <router-view name="addNewUser"></router-view>
      <router-view name="changeAuthor"
      :authorId="author" 
      :author="options.authors[author]"></router-view>
      <!-- <show-add-athor v-if="showAddAuthor== true" @addAuthor="addAuthor"></show-add-athor> -->

      <show-change-author v-if="showChangeAuthor== true" @changeAuthor="changeAuthor" 
      @deleteAuthor="deleteAuthor" :authorId="author" :author="options.authors[author]">
      </show-change-author>

      <!-- <show-add-genre v-if="showAddGenre== true" @addGenre="addGenre"></show-add-genre> -->

      <show-change-genre v-if="showChangeGenre== true" @changeGenre="changeGenre" @deleteGenre="deleteGenre"
      :genreId="genre" :genre="options.genres[genre]"></show-change-genre>

        <show-add-book v-if="showAddBook== true" @addBook="addBook" :genres="options.genres"
       :authors="options.authors"></show-add-book>

       <show-change-book v-if="showChangeBook== true" @changeBook="changeBook" :book="options.books[book]"
       :authors="options.authors" :genres="options.genres"></show-change-book>

       <!-- <show-add-user v-if="showAddUser== true" @createUser="createUser"></show-add-user> -->

       <show-change-user v-if="showChangeUser==true" :user="options.users[user]"
        @changeUser="changeUser"></show-change-user>

  </div>

</div> 
</template>

<script>
// import ShowAddAthor from '@/components/addAdminComponents/addNewAuthor'
// import ShowAddGenre from '@/components/addAdminComponents/addNewGenre'
