import ShowChangeBook from '@/components/changeAdminComponents/changeBook'
import ShowChangeUser from '@/components/changeAdminComponents/changeUser'
export default {
  name: 'HelloWorld',
  data () {
    return {
      author: '',
      genre: '',
      book: '',
      newBook: {
        'price': '',
        'name': '',
        'description': '',
        'discount': '',
        'genreId': [],
        'authorId': []},
      user: '',
      a:1,
      b:'',
      showAddAuthor: false,
      showChangeAuthor: false,
      showAddGenre: false,
      showChangeGenre: false,
      showAddBook: false,
      showChangeBook : false,
      showAddUser : false,
      showChangeUser : false,
      options: {
        authors: '',
        genres: '',
        books: '',
        users: '',
        roles: {
          1:{
            'id':1,
            'role': 'admin'
          },
          2:{
            'id':2,
            'role': 'user'
          }
        },
        userStatus: {
          1:{
            'id': 1,
            'status': 'active'
          },
          2:{
            'id': 2,
            'status': 'no active'
          }
        }
      }
      // msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    addAuthorField: function () {
      this.$router.push({ path: '/adminPanel/addNewAuthor'} );
    },
    addAuthor: function (name,surname) {
      var self = this;
      fetch(this.$parent.authorUrl, {
          method: 'post',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'name='+JSON.stringify(name)+'&surname='+JSON.stringify(surname)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            fetch(self.$parent.authorUrl,{  
                method: 'get'})
            .then(self.$parent.status)
            .then(self.$parent.json)
            .then(function(data){
              if(data != false)
              {
                self.options.authors = data;       
                alert('Author successful added');
                self.$router.push({ path: '/adminPanel'} );
              }
            });
          }
      });

    },
    prihodit: function () {
      // console.log('prihod')
      this.showChangeAuthor = false
      this.showChangeAuthor = false
      this.showChangeGenre = false
      this.showChangeBook = false
      this.showChangeUser = false
            // this.a--
              // console.log(this.a)
            // if(this.a % 2 !==0){
            //   console.log(this.a)
