      // this.a++
      // console.log('clik')
                  if(this.a % 2 ==0){
              // console.log(this.a)
              this.showChangeAuthor = true;
            }
    },
    changeAuthorField: function () {
      this.$router.push({ path: '/adminPanel'} );
      // console.log('change')
      // this.showChangeAuthor = false;
      // console.log(this.showChangeAuthor)
      // this.a++
      // if(this.a % 2){}
      // delete this.a;
      // if(this.b){

      // this.a = 'show-change-author';
      // // this.$destroyed('show-change-author')
      // }else{
      //   this.b = 'show-change-author';
      // }
      // this.newAuthor.name = this.options.authors[this.author].name;
      // this.newAuthor.surname = this.options.authors[this.author].surname;
      // this.newAuthor.id = this.author;
      // this.showAddGenre = false,
      // this.showChangeGenre = false,
      // this.showAddBook = false,
      // this.showAddAuthor = false;
      // if(this.showChangeAuthor){

      // this.showChangeAuthor = false;
      // }else{

      // this.showChangeAuthor = true;
      // }
      this.showChangeAuthor = true;
    },
    changeAuthor: function (id,name,surname) {
      var self = this;
      fetch(this.$parent.authorUrl, {
          method: 'put',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'id='+JSON.stringify(id)+'&name='+JSON.stringify(name)+'&surname='+JSON.stringify(surname)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
        console.log(data);
          if(data == true){
            alert('Author successful changed')
            var changedAuthor = {'id':id,'name':name,'surname':surname}
            self.options.authors[id] = changedAuthor;
            self.unshowWorkFiel();
          }
      });
    },
    deleteAuthor: function (id) {
      var self = this;
      fetch(this.$parent.authorUrl+id,{  
            method: 'delete'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          if(data == true)
          {
            alert('Author successful deleted');
            self.$delete(self.options.authors, id);
            self.unshowWorkFiel();
          }    
      });
    },
    addGenreField: function () {
      this.$router.push({ path: '/adminPanel/addNewGenre' });
    },
    addGenre: function (name) {
      var self = this;
      fetch(this.$parent.genreUrl, {
          method: 'post',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'name='+JSON.stringify(name)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            fetch(self.$parent.genreUrl,{  
                method: 'get'})
            .then(self.$parent.status)
            .then(self.$parent.json)
            .then(function(data){
              if(data != false)
              {
                self.options.genres = data;       
                self.$router.push({ path: '/adminPanel'} );
                alert('Genre successful added');
                self.unshowWorkFiel();
              }
            });
          }
      });
    },
    changeGenreField: function () {
      // console.log(this.options.genres[this.genre]);
      // this.newGenre.genre = this.options.genres[this.genre].genre;
      this.showChangeAuthor = false,
      this.showAddGenre = false,
      this.showAddBook = false,
      this.showAddAuthor = false;
      this.showChangeGenre = true;

    },
    changeGenre: function (id,name) {
      var self = this;
      fetch(this.$parent.genreUrl, {
          method: 'put',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'id='+JSON.stringify(id)+'&name='+JSON.stringify(name)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            alert('Genre successful changed')
            var changedGenre = {'id':id,'name':name}
            self.options.genres[id] = changedGenre;
            self.unshowWorkFiel();
          }
      });

      
    },
    deleteGenre: function (id) {
      var self = this;
      fetch(this.$parent.genreUrl+id,{  
            method: 'delete'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          if(data == true)
          {
            alert('Genre successful deleted')
            self.$delete(self.options.genres, id)
            self.unshowWorkFiel();
          }    
      });
    },
    addBookField: function () {
      // this.$router.push({ path: '/adminPanel/addNewBook' , props: { genres:options.genres,
      //  authors:options.authors }});
      this.$router.push({ path: '/adminPanel/addNewBook'});
      // this.showChangeAuthor = false,
      // this.showAddGenre = false,
      // this.showChangeGenre = false,
      // this.showAddAuthor = false;
      // this.showAddBook = true;
    },
    addBook: function (name,description,price,discount,genres,authors) {
      var self = this;
      fetch(this.$parent.bookUrl, {
          method: 'post',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'name='+JSON.stringify(name)+'&description='+JSON.stringify(description)
          +'&price='+JSON.stringify(price)+'&discount='+JSON.stringify(discount)
          +'&genres='+JSON.stringify(genres)+'&authors='+JSON.stringify(authors)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            fetch(self.$parent.bookUrl,{  
                method: 'get'})
            .then(self.$parent.status)
            .then(self.$parent.json)
            .then(function(data){
              if(data != false ){
                self.options.books = data;    
                self.$router.push({ path: '/adminPanel'} );
                alert('Book successful added');
                self.unshowWorkFiel();
              }
            });

          }
      });
    },
    changeBookField: function () {
      this.showChangeBook = true;
    },
    changeBook: function (id,name,description,price,discount,
        newBookGenres,newBookAuthors,oldGenres,oldAuthors) {
      var self = this;
      fetch(this.$parent.bookUrl, {
          method: 'put',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'name='+JSON.stringify(name)+'&description='+JSON.stringify(description)
          +'&price='+JSON.stringify(price)+'&discount='+JSON.stringify(discount)
          +'&newGenres='+JSON.stringify(newBookGenres)+'&newAuthors='+JSON.stringify(newBookAuthors)
          +'&oldAuthors='+oldAuthors+'&oldGenres='+oldGenres+'&bookId='+JSON.stringify(id)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            alert('Book successful Changed');
            self.unshowWorkFiel();
            var changedBook = {'id':id,'name':name,'description':description,'price':price,
            'discount':discount,'genres':newBookGenres,'authors':newBookAuthors};
            self.options.books[id] = changedBook;
          }
      });
    },
    addUserField: function () {
      this.$router.push({ path: '/adminPanel/addNewUser'} );
      // this.showAddUser = true;
    },
    createUser: function (login,password,name,surname,
                  phone,email,isActive,role,discount) {
      var self = this;
      fetch(this.$parent.authUrl, {
          method: 'post',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'login='+JSON.stringify(login)+'&password='+JSON.stringify(password)
          +'&name='+JSON.stringify(name)+'&surname='+JSON.stringify(surname)
          +'&phone='+JSON.stringify(phone)+'&email='+JSON.stringify(email)
          +'&isActive='+JSON.stringify(isActive)+'&role='+JSON.stringify(role)
          +'&discount='+JSON.stringify(discount)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
              alert('User successfully added');
              self.$router.push({ path: '/adminPanel'} );
          } else if(data == 'login already exists'){
              alert('login already exists');
          }else{
              alert('Added error');
          }
      });
      this.showAddUser = false;
    },
    changeUserField: function () {
      this.showChangeUser = true;
    },
    changeUser: function (login,password,name,surname,
                  phone,email,isActive,role,discount,userId) {
                    console.log(2222);
      var self = this;
      fetch(this.$parent.userUrl, {
          method: 'put',
          headers: {  
              "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
          },  
          body: 'login='+JSON.stringify(login)+'&password='+JSON.stringify(password)
          +'&name='+JSON.stringify(name)+'&surname='+JSON.stringify(surname)
          +'&phone='+JSON.stringify(phone)+'&email='+JSON.stringify(email)
          +'&isActive='+JSON.stringify(isActive)+'&role='+JSON.stringify(role)
          +'&discount='+JSON.stringify(discount)+'&id='+JSON.stringify(userId)
      })
      .then(this.$parent.status)
      .then(this.$parent.json)
      .then(function (data) {
          if(data == true){
            alert('User successful Changed');
          }
      });
      this.showChangeUser = false;
    },
    getLastKey: function (object){
      var min = Infinity, max = -Infinity, lastKey;
      for(lastKey in object) {
          if( object[lastKey] < min) min = object[lastKey];
          if( object[lastKey] > max) max = object[lastKey];
      }
      return lastKey;
    },
    unshowWorkFiel: function (){
      this.showAddAuthor= false;
      this.showChangeAuthor= false;
      this.showAddGenre= false;
      this.showChangeGenre= false;
      this.showAddBook= false;
      this.showChangeBook = false;
      this.showAddUser = false;
      this.showChangeUser = false;
      // this[param] = true;
    }
  },
  components: {
    // ShowAddAthor,
    // ShowAddGenre,
    // ShowAddBook,
    // ShowAddUser,
    ShowChangeAuthor,
    ShowChangeGenre,
    ShowChangeBook,
    ShowChangeUser
  },
  created () {
    var self = this;
      fetch(this.$parent.authorUrl,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          self.options.authors = data;       
      });

      fetch(this.$parent.genreUrl,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          self.options.genres = data;        
      });

      fetch(this.$parent.bookUrl,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          self.options.books = data;
          console.log(data);
          // self.options.genres = data;        
      // console.log(self.options.books)
      });

      fetch(this.$parent.userUrl,{  
            method: 'get'})
        .then(this.$parent.status)
        .then(this.$parent.json)
        .then(function(data){
          // console.log(data);
          // self.options.books = data;
          // console.log(data);
          self.options.users = data;        
      // console.log(self.options.books)
      });
      // var c = [1,2];
      // console.log(JSON.stringify(c))
      // console.log(c[1])
  },
